import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { FileImportDataSearchReplaceViewModel } from "../../Model/ViewModel/FileImportDataSearchReplaceViewModel";

/**
 * Generated class for the FileImportDataSearchReplaceModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-import-data-search-replace-add-edit-modal",
  templateUrl: "file-import-data-search-replace-add-edit-modal.html"
})
export class FileImportDataSearchReplaceAddEditModalPage {
  item: FileImportDataSearchReplaceViewModel;
  mode: string;
  title: string;
  CanEditBatch: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.item = new FileImportDataSearchReplaceViewModel();
    this.item.schemaname = navParams.data.item.schemaname;
    this.item.tablename = navParams.data.item.tablename;
    this.item.columnname = navParams.data.item.columnname;
    this.item.search_value = navParams.data.item.search_value;
    this.item.replace_value = navParams.data.item.replace_value;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;

    this.mode = navParams.data.mode;
    this.CanEditBatch = navParams.data.CanEditBatch;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileImportDataSearchReplaceModalPage");
  }
}



